<template>
  <v-card
    v-resize="onResize"
    id="table"
    elevation="0"
    :class="{
      'slide-leave slide-enter-active': this.$store.state.sheet && hasDetail,
      'slide-leave-to slide-leave-active': !this.$store.state.sheet && hasDetail
    }"
    :style="getTableHeight"
  >
    <div
      class="v-data-table pl-2 v-data-table--dense v-data-table--fixed-height theme--light"
    >
      <div class="v-data-table__wrapper" :style="getTableHeight">
        <table class="v-data-table-body">
          <thead class="v-data-table-header">
            <tr v-for="(row, id) in headerRows" :key="id">
              <th
                v-for="(h, i) in row"
                :key="i"
                :class="getAlign(h, h.textMinify)"
                :rowspan="h.children ? 1 : headerDeep"
                :colspan="getColspan(h, id)"
              >{{ h.text | getHeaderText }}</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(item, i) in tableData"
              :key="i"
              @click="selectRow(i)"
              :class="{ 'selected-row': i === selectedItem }"
            >
              <td
                v-for="(header, col) in headersFlat"
                :key="col"
                :class="cellClasses[i + '_' + header.value]"
                @click="
                  openDetail(
                    $event,
                    item[header.value + '_request'],
                    header.value
                  ),
                    highlightCell(i, header, item)
                "
              >
                <v-tooltip
                  right
                  v-if="item[header.value + '_tooltip']"
                  max-width="190"
                  open-delay="2"
                  transition="fade-transition"
                >
                  <template v-slot:activator="{ on }">
                    <div
                      v-on="on"
                      v-if="
                        isEditable(item, header.editable, item[header.value])
                      "
                    >
                      <EditCell
                        @updateCell="
                          changeClass(
                            $event,
                            item[header.value],
                            item,
                            header,
                            i
                          )
                        "
                        :oldValue="item[header.value]"
                        :row="item"
                        :col="header"
                        :rowId="i"
                      />
                    </div>
                    <div
                      v-on="on"
                      v-else
                      :class="{
                        'detail-cell':
                          selectedCell.i === i &&
                          selectedCell.dataIndex === header.value
                      }"
                    >
                      {{
                        item[header.value]
                          | customizeValue(
                            header.value,
                            item,
                            tableType,
                            tableData,
                            i
                          )
                      }}
                    </div>
                  </template>
                  <span>{{
                    getTooltip(
                      item[header.value + '_tooltip'],
                      item[header.value]
                    )
                  }}</span>
                </v-tooltip>
                <template v-else>
                  <EditCell
                    @updateCell="
                      changeClass($event, item[header.value], item, header, i)
                    "
                    :oldValue="item[header.value]"
                    :row="item"
                    :col="header"
                    :rowId="i"
                    v-if="isEditable(item, header.editable, item[header.value])"
                  />
                  <div
                    v-else
                    :class="{
                      'detail-cell':
                        selectedCell.i === i &&
                        selectedCell.dataIndex === header.value
                    }"
                  >
                    {{
                      item[header.value]
                        | customizeValue(
                          header.value,
                          item,
                          tableType,
                          tableData,
                          i
                        )
                    }}
                  </div>
                </template>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </v-card>
</template>

<script>
import axios from 'axios';
import EditCell from './EditCell';
export default {
  name: 'DataTable',
  components: {
    EditCell
  },
  created() {
    if (!this.isDetail)
      this.$store.state.defaultFiltersValues = Object.assign({}, this.params);
    this.fetchTableData(this.params);
    this.$store.state.sheet = false;
  },
  filters: {
    getHeaderText(text) {
      let headText = text;
      if (text.indexOf('<br>') >= 0) {
        headText = text.replace('<br>', '\n');
      }
      return headText;
    },
    customizeValue(value, col, record, tableType, data, recInd) {
      let val = value;
      if (tableType === 'norm_depo' && col === 'sort_number') {
        if (record.type === '') {
          let ind = 0;
          data.every(function (rec) {
            if (rec.type === '') {
              ind++;
            }
            return record !== rec;
          });
          return ind;
        } else return '';
      }
      if (col === 'row_number') {
        //значения для простого нумерованного столбца
        let i = 0;
        data.every(function (rec) {
          i++;
          return record !== rec;
        });
        return i;
      }
      if (tableType === 'norm_ratio')
        if (typeof value === 'number') return Math.floor(val * 100) / 100;
      if (typeof value === 'string' && value.indexOf('<b>') >= 0) {
        if (value.indexOf('</b>') >= 0) {
          val = value.substring(3, value.length - 4).trim();
        } else {
          let tempVal = value.substring(3, value.length - 3).trim();
          if (tempVal.indexOf('<b>') >= 0) {
            val = tempVal.substring(3, tempVal.length - 3).trim();
          } else val = tempVal;
        }
      }
      if (
        tableType === 'st_tasks' &&
        (col === 'road' || col === 'station_name')
      ) {
        if (recInd - 1 >= 0 && record[col] === data[recInd - 1][col]) val = '';
      }
      return val;
    }
  },
  computed: {
    getTableHeight() {
      const FILTERS_CONTENT_HEIGHT = 138,
        BLOCKS_HEIGHT = 210,
        DETAIL_HEIGHT = 65,
        OPEN_DETAIL_CONTENT = 190,
        INFO_HEIGHT = 30;
      let tableHeight = this.windowSize.y - BLOCKS_HEIGHT;

      if (this.hasDetail) tableHeight -= DETAIL_HEIGHT;
      if (this.hasInfo) tableHeight -= INFO_HEIGHT;

      if (this.$store.state.filterOpen) tableHeight -= FILTERS_CONTENT_HEIGHT;
      if (this.$store.state.sheet) tableHeight -= OPEN_DETAIL_CONTENT;

      if (this.isDetail) tableHeight = 185;

      return `height: ${tableHeight}px`;
    }
  },
  methods: {
    changeClass(val, oldVal, it, col, i) {
      this.setCellStyles(it, i, col, val);
      this.changeValue(val, col.value, i, oldVal);
      this.$store.state.cancelBtnActive = !this.editedValues.length > 0;
      this.$store.state.recalcBtnActive = !this.editedValues.length > 0;
      this.$forceUpdate();
    },
    changeValue(value, col, id, oldValue) {
      let isValueExist;
      this.editedValues.forEach((editedValue, i, arr) => {
        if (editedValue.id === id && editedValue.col === col) {
          if (+value !== +oldValue) {
            editedValue.value = value;
          } else {
            arr.splice(i);
          }
          isValueExist = true;
        }
      });
      if (!isValueExist) {
        this.editedValues.push({ value, col, id });
      }
    },
    highlightCell(i, col, it) {
      if (
        (it[col.value + '_request'] || col.value === 'duration') &&
        it.type !== 'GG' &&
        it.type !== 'SS' &&
        it.type !== 'G' &&
        it.type !== 'S' &&
        it.type !== 'I' &&
        (it[col.value] || it[col.value] === 0)
      ) {
        if (
          this.selectedCell.i === i &&
          this.selectedCell.dataIndex === col.value
        ) {
          this.selectedCell.i = null;
          this.selectedCell.dataIndex = null;
          this.$store.state.sheet = false;
        } else {
          this.selectedCell.i = i;
          this.selectedCell.dataIndex = col.value;
        }
      }
    },
    openDetail(e, url, key) {
      if (url || key === 'duration') {
        if (!this.$store.state.sheet) {
          setTimeout(
            () =>
              e.target.scrollIntoView({
                block: 'end',
                inline: 'nearest',
                behavior: 'smooth'
              }),
            50
          );
        }
        this.$store.state.sheet = true;
        this.$emit('loadDetail', url || '');
      }
    },
    selectRow(rowNumber) {
      this.selectedItem = rowNumber;
    },
    getAlign(header, info) {
      let cl = '';
      if (info === 'mini') {
        cl += 'smaller-text ';
      }
      if (header.border) {
        cl += 'thick-border-bottom ';
      } else cl += 'thin-border-bottom ';
      if (!header.alignHeader) {
        cl += 'align-text-left';
      }
      return cl;
    },
    getTooltip(tooltipText, value) {
      if (tooltipText) {
        if (
          this.tableType === 'norm_sec' ||
          this.tableType === 'reg_join' ||
          this.tableType === 'reg_sec'
        ) {
          let ODZ = tooltipText.split(';'),
            minODZ = Number(ODZ[0].replace(/\D+/g, '')),
            maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
            originValue,
            approveUser = '';

          if (Number(ODZ[2].replace(/\D+/g, '')))
            originValue = Number(ODZ[2].replace(/\D+/g, ''));
          if (ODZ[3].split(' = ')[1] !== '')
            approveUser = ODZ[3].split(' = ')[1];

          if (ODZ) {
            if (approveUser && originValue && value !== originValue) {
              return (
                'Данные изменены: ' +
                approveUser +
                ' с ' +
                originValue +
                ' на ' +
                value +
                '; мин = ' +
                minODZ +
                '; макс = ' +
                maxODZ
              );
            } else {
              return 'мин = ' + minODZ + '; макс = ' + maxODZ;
            }
          } else {
            return 'мин = ' + minODZ + '; макс = ' + maxODZ;
          }
        } else return tooltipText;
      } else return '';
    },
    isEditable(it, ed, value) {
      return (
        value !== undefined &&
        ed &&
        it.type !== 'S' &&
        it.type !== 'SS' &&
        it.type !== 'I'
      );
    },
    setCellStyles(it, ind, col, newValue) {
      let cl_string = '';
      if (
        +it[col.value] ||
        +it[col.value] === 0 ||
        (typeof it[col.value] === 'string' &&
          it[col.value].indexOf('<b>') >= 0 &&
          (+it[col.value].substring(3, it[col.value].length - 4) ||
            +it[col.value].substring(3, it[col.value].length - 4) === 0)) ||
        (typeof it[col.value] === 'string' &&
          it[col.value].indexOf('<b><b>') >= 0 &&
          (+it[col.value].substring(6, it[col.value].length - 6) ||
            +it[col.value].substring(6, it[col.value].length - 6) === 0))
      ) {
        cl_string += 'numbers-font-size ';
      }
      if (
        (col.editable ||
          it[col.value + '_request'] ||
          col.value === 'duration') &&
        it.type !== 'GG' &&
        it.type !== 'SS' &&
        it.type !== 'G' &&
        it.type !== 'S' &&
        it.type !== 'I' &&
        (it[col.value] || +it[col.value] === 0)
      ) {
        if (it[col.value + '_request'] || col.value === 'duration') {
          cl_string += 'detail-cell-main ';
        } else {
          if (newValue && +newValue !== +it[col.value]) {
            cl_string += 'edited-cell-main ';
          } else cl_string += 'edit-cell-main ';
        }
      }
      if (this.tableType === 'reg_join') {
        if (col.editable && it[col.value] === 0) cl_string += 'back-pink ';
      }
      if (
        it.type === 'GG' ||
        it.type === 'SS' ||
        it.type === 'G' ||
        it.type === 'S' ||
        it.type === 'I' ||
        (typeof it[col.value] === 'string' && it[col.value].indexOf('<b>') >= 0)
      )
        cl_string += 'bold-text ';
      if (col.fixedColumn) cl_string += 'fixed-border ';
      if (it[col.value + '_color']) {
        if (it[col.value + '_color'] === 'yellow') cl_string += 'back-yellow ';
      }
      if (it[col.value + '_tooltip'] && this.tableType !== 'norm_depo') {
        if (
          typeof value === 'string' &&
          it[col.value + '_tooltip'].indexOf(';') >= 0
        ) {
          let ODZ = it[col.value + '_tooltip'].split(';'),
            minODZ = Number(ODZ[0].replace(/\D+/g, '')),
            maxODZ = Number(ODZ[1].replace(/\D+/g, '')),
            maxODZ10 = Math.ceil(maxODZ * 1.1),
            originValue,
            approveUser = '',
            value = newValue ? newValue : it[col.value];

          if (Number(ODZ[2].replace(/\D+/g, '')))
            originValue = Number(ODZ[2].replace(/\D+/g, ''));
          if (ODZ[3].split(' = ')[1] !== '')
            approveUser = ODZ[3].split(' = ')[1];

          if (ODZ) {
            if (
              approveUser &&
              (originValue || originValue === 0) &&
              value !== originValue
            ) {
              if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                cl_string += 'back-pink edited-cell-main ';
              } else if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                cl_string += 'back-pink ';
              } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
                cl_string += 'back-yellow edited-cell-main ';
              }
            } else {
              if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                cl_string += 'back-pink edit-cell-main ';
              } else if (
                !(value >= minODZ && value <= maxODZ) &&
                (value === 0 || value > maxODZ10)
              ) {
                cl_string += 'back-pink ';
              } else if (value !== 0 && value > maxODZ && value <= maxODZ10) {
                cl_string += 'back-yellow edit-cell-main ';
              }
            }
          }
        }
      }
      this.cellClasses[ind + '_' + col.value] = cl_string;
    },
    fetchTableData(params) {
      if (!this.isDetail || (this.isDetail && this.$store.state.sheet)) {
        axios
          .get(this.url || params, {
            params: !this.isDetail ? params : ''
          })
          .then((response) => {
            this.headerDeep = this.deepSearch(
              this.headers || this.headersRemote,
              0,
              []
            );
            if (isFinite(this.headerDeep)) {
              for (let i = 1; i <= this.headerDeep; i++) {
                let row = this.getSubHeader(
                  this.headers || this.headersRemote,
                  i,
                  0,
                  []
                );
                this.headerRows.push(row);
              }
            }

            if (!this.isRemote || (this.isRemote && params.load)) {
              this.tableData = response.data.data;
              if (!this.isRemote) this.createHeadersFlat();
              this.tableData.forEach((row, i) => {
                this.headersFlat.forEach((col) => {
                  this.setCellStyles(row, i, col);
                });
              });
            } else {
              this.createHeaders(response.data.settings);
              if (!this.isDetail) params.load = true;
              this.fetchTableData(params);
            }
          });
      }
    },
    setHeaders(settings, config) {
      for (let key in settings) {
        let headItem = {};
        if (settings[key].visible) {
          if (settings[key].child) {
            headItem = {
              text: settings[key].name,
              alignHeader: 'center',
              value: key,
              children: []
            };
            this.setHeaders(settings[key].child, headItem.children);
          } else {
            headItem = {
              text: settings[key].name,
              alignHeader: 'center',
              value: key,
              border: true
            };
          }
          config.push(headItem);
        }
      }
      return config;
    },
    createHeaders(settings) {
      this.headersRemote = this.setHeaders(settings, []);
      this.createHeadersFlat();
    },
    createHeadersFlat() {
      let headersArray = this.headers || this.headersRemote;
      this.setHeadersFlat(headersArray);
    },
    setHeadersFlat(headersArray) {
      for (let key in headersArray) {
        if (headersArray[key].children) {
          this.setHeadersFlat(headersArray[key].children);
        } else this.headersFlat.push(headersArray[key]);
      }
    },
    onResize() {
      this.windowSize = { x: window.innerWidth, y: window.innerHeight };
    },
    getSubHeader(headers, rowId, counter, arr) {
      counter++;
      if (rowId === counter) {
        headers.forEach((v) => {
          if (rowId > 1) {
            arr.push(v);
          } else arr = arr.concat(v);
        });
      } else {
        headers
          .filter((i) => i.children)
          .forEach((v) => {
            this.getSubHeader(v.children, rowId, counter, arr);
          });
      }
      return arr;
    },
    deepSearch(obj, counter, arr) {
      counter++;
      for (let key in obj) {
        if (obj[key].children) {
          this.deepSearch(obj[key].children, counter, arr);
        }
        if (arr) arr.push(counter);
      }
      return Math.max.apply(null, arr);
    },
    calculateColspan(obj, arr) {
      if (obj.children) {
        let chLength = obj.children.length;
        obj.children.forEach((c) => {
          if (c.children) {
            chLength -= 1;
            this.calculateColspan(c, arr);
          }
        });
        arr.push(chLength);
      }
      return arr.reduce((res, el) => res + el);
    },
    getColspan(col) {
      let colspan;
      if (col.children) {
        colspan = this.calculateColspan(col, []);
      } else colspan = 1;
      return colspan;
    }
  },
  props: [
    'headers',
    'url',
    'params',
    'tableType',
    'isDetail',
    'hasDetail',
    'hasInfo',
    'isRemote'
  ],
  data() {
    return {
      editedValues: [],
      selectedItem: null,
      cellClasses: {},
      selectedCell: {
        i: null,
        dataIndex: null
      },
      windowSize: {
        x: 0,
        y: 0
      },
      tableData: [],
      headersRemote: [],
      headersFlat: [],
      headerRows: [],
      headerDeep: 1
    };
  }
};
</script>

