<template>
  <div class="pa-5 pb-0">
    <Filters
      :filters="[{ filterName: 'date', filterType: 'date', labelText: 'Дата' }]"
      @reloadTable="updateTable()"
    />
    <Buttons :params="params" />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :url="url"
      :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import Filters from '../components/Filters';
import Buttons from '../components/Buttons';
import Tabs from '../components/Tabs';
import DataTable from '../components/DataTable';

export default {
  name: 'NormalizationDepo',
  components: { Buttons, Filters, Tabs, DataTable },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      url: '/arm-isujt/server/norm_depo.json',
      tableType: 'norm_depo',
      params: {
        buttons: {
          print: true
        },
        name: 'Оперативное нормирование',
        icon: 'icon icon-trending-up'
      },
      tabModel: 1,
      headers: [
        {
          text: '№',
          align: 'start',
          value: 'road_code',
          textMinify: 'mini',
          border: true
        },
        {
          text: 'Депо приписки локомотивов',
          align: 'start',
          alignHeader: 'center',
          value: 'depot_name',
          textMinify: 'mini',
          border: true
        },
        {
          text:
            'Потребное количество эксплуатируемого\nпарка локомотивов на следующие сутки',
          align: 'start',
          cols: 4,
          alignHeader: 'center',
          children: [
            {
              text: 'Т (4-х), т.ед.',
              value: 's',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'Т (3-х), т.ед.',
              value: 't',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'Л (2-х), т.ед.',
              value: 'l',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'Всего, т.ед.',
              value: 'sum',
              alignHeader: 'center',
              border: true
            }
          ]
        }
      ]
    };
  }
};
</script>
