<template>
  <v-btn
    class="cancel-btn mr-auto"
    :class="{ 'disable-events': this.$store.state.cancelBtnActive }"
    color="#FF7141"
    min-height="29px"
    ref="cancel"
    small
    dark
    depressed
    @click="cancelChange"
    :ripple="false"
  >
    Отмена
  </v-btn>
</template>

<script>
export default {
  name: 'CancelButton',
  methods: {
    cancelChange() {
      this.$store.state.cancelBtnActive = true;
      this.$store.state.saveBtnActive = true;
      this.$store.state.recalcBtnActive = true;
    }
  }
};
</script>

<style scoped lang="sass">
button.cancel-btn
  width: 120px
  padding-top: 2px !important
  font: 14px/17px $body-font-family
  letter-spacing: 0.01px
  text-transform: none

.disable-events
  pointer-events: none
  opacity: 0.5
</style>
