<template>
  <v-btn
    class="export pl-0 pr-0"
    x-small
    text
    @click="exportExcel"
    :ripple="false"
  >
    <span class="icon-side icon icon-excel"></span>
    Выгрузить в Excel
  </v-btn>
</template>

<script>
export default {
  name: 'ExportButton',
  props: ['url'],
  methods: {
    exportExcel() {
      window.open('/ajax.php?module=ExcelController&' + this.url);
    }
  }
};
</script>

<style scoped lang="sass">
button.export
  font: 14px/17px $body-font-family
  letter-spacing: normal
  text-transform: none
  color: $color-dark-blue
  opacity: 1
  &::after
    content: ''
    position: absolute
    background-color: $color-dark-blue
    left: 20px
    right: 0
    bottom: 1px
    height: 1px
    transition: background-color $transition-normal
  &::before
    color: transparent
  &:hover
    &::after
      background-color: transparent

.icon-side
  margin-right: 6px
  font-size: 14px
</style>
