          border: true
        },
        {
          text: 'Продолжительность\n«Окна» (час)',
          value: 'short_oper',
          border: true
        },
        {
          text: 'Описание места\nпроведения (перегон)',
          value: 'time_oper',
          border: true
        },
        {
          text: 'Описание\nвыполняемых работ',
          value: 'location',
          border: true
        }
      ],
      tabModel: 0,
      tableType: 'norm_sec',
      headers: [
        {
          text: 'Тяговый полигон',
          align: 'start',
          textMinify: 'mini',
          children: [
            {
              text: 'Наименование\nрасчетных участков',
              value: 'long_name',
              textMinify: 'mini',
              border: true
            }
          ]
        },
        { text: 'Длина\n(L,км)', value: 'distance', border: true },
        { text: 'Коэф.\nКп.гр.э', value: 'k_pgre', border: true },
        { text: 'Коэф.\nКп.п.э.', value: 'k_pe', border: true },
        {
          text: 'Кол-во\nпоездов\nN(max)',
          value: 'tr_num',
          border: true,
          editable: true,
          tooltip: true
        },
        {
          text: 'В т.ч. тяжело\nвесных N\n(2-ая тяга\nСМЕТ)',
          value: 'tr_heavy_num',
          border: true
        },
        { text: 'Окна,\nчас', value: 'duration', border: true },
        {
          text: 'Потребность локомотивов',
          value: 'way',
          cols: 15,
          alignHeader: 'center',
          children: [
            {
              text: 'Грузовое движение',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 'ft', border: true },
                { text: 'Мт\n(3-х)', value: 'ft3', border: true },
                { text: 'Мт\n(2-х)', value: 'fl', border: true }
              ]
            },
            {
              text: 'ПВД',
              alignHeader: 'center',
              children: [
                {
                  text: 'Мт\n(3-х)',
                  value: 'locl3',
                  border: true,
