                },
                {
                  text: 'Мт\n(2-х)',
                  value: 'locl',
                  border: true,
                  editable: true
                }
              ]
            },
            {
              text: 'ТОЛКАЧИ',
              alignHeader: 'center',
              children: [
                {
                  text: 'Мт\n(4-х)',
                  value: 'pt4',
                  border: true,
                  editable: true
                },
                {
                  text: 'Мт\n(3-х)',
                  value: 'pt',
                  border: true,
                  editable: true
                },
                { text: 'Мт\n(2-х)', value: 'pl', border: true, editable: true }
              ]
            },
            {
              text: 'ОПЕР, РД',
              alignHeader: 'center',
              children: [
                {
                  text: 'Мт\n(4-х)',
                  value: 'rt4',
                  border: true,
                  editable: true
                },
                {
                  text: 'Мт\n(3-х)',
                  value: 'rt',
                  border: true,
                  editable: true
                },
                { text: 'Мт\n(2-х)', value: 'rl', border: true, editable: true }
              ]
            },
            {
              text: 'ВСЕГО',
              alignHeader: 'center',
              children: [
                { text: 'Мт\n(4-х)', value: 't', border: true },
                { text: 'Мт\n(3-х)', value: 't3', border: true },
                { text: 'Мт\n(2-х)', value: 'l', border: true }
              ]
            },
            {
              text: 'ИТОГО',
              alignHeader: 'center',
              children: [{ text: 'Мв', value: 'total', border: true }]
            }
          ]
        }
      ]
    };
  },
  created() {
    this.$store.state.detailInfo.detailName =
      'Планируемые окна на расчетном участке';
  },
  computed: {
    createExportUrl() {
      return (
        'method=norm_1&tab=panel-reportOP&date=' +
        this.getFormattedDate(this.$store.state.filtersValues.date)
      );
    }
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: this.$store.state.filtersValues.drl
      });
    },
    setDetail(url) {
      this.detailUrl = url;
      // this.$refs.detailPanel.updateDetail(this.detailUrl);
      this.$refs.detailPanel.updateDetail('../../server/analize_details.json'); //пока тестовые данные
    }
  }
};
</script>
