<template>
  <v-btn
    class="print pl-0 pr-0"
    x-small
    text
    @click="printTable"
    :ripple="false"
  >
    <span class="icon-side icon icon-print"></span>
    Печать
  </v-btn>
</template>

<script>
export default {
  name: 'PrintButton',
  methods: {
    printTable() {
      let prtContent = document.getElementById('table'),
        WinPrint = window.open('');

      WinPrint.document.write(prtContent.innerHTML);
      WinPrint.document.close();
      WinPrint.print();
    }
  }
};
</script>

