import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import NormalizationSections from '../views/NormalizationSections';
import NormalizationDepo from '../views/NormalizationDepo';
import NormalizationRatio from '../views/NormalizationRatio';
import RegulationJoints from '../views/RegulationJoints';
import RegulationSections from '../views/RegulationSections';
import RegulationAnalysis from '../views/RegulationAnalysis';
import Control from '../views/Control';
import Normalization from '../views/Normalization';
import Regulation from '../views/Regulation';
import Planning from '../views/Planning';
import PlanningTechStationTasks from '../views/PlanningTechStationTasks';
import PlanningJointsTasks from '../views/PlanningJointsTasks';

Vue.use(VueRouter);

const routes = [
  {
    path: '/tasks/',
    name: 'Home',
    component: Home
  },
  {
    path: '/control/',
    component: Control,
    children: [
      {
        path: 'norm/',
        component: Normalization,
        children: [
          {
            path: 'section',
            component: NormalizationSections
          },
          {
            path: 'depo',
            component: NormalizationDepo
          },
          {
            path: 'ratio',
            component: NormalizationRatio
          }
        ]
      },
      {
        path: 'plan/',
        component: Planning,
        children: [
          {
            path: 'st_tasks',
            component: PlanningTechStationTasks
          },
          {
            path: 'join_tasks',
            component: PlanningJointsTasks
          },
          {
            path: 'planning',
            component: PlanningJointsTasks
          },
          {
            path: 'lc_task',
            component: PlanningJointsTasks
          },
          {
            path: 'lc_plan',
            component: PlanningJointsTasks
          },
          {
            path: 'plan_ind',
            component: PlanningJointsTasks
          },
          {
            path: 'graf_loco',
            component: PlanningJointsTasks
          }
        ]
      },
      {
        path: 'reg/',
        component: Regulation,
        children: [
          {
            path: 'joint',
            component: RegulationJoints
          },
          {
            path: 'section',
            component: RegulationSections
          },
          {
            path: 'analysis',
            component: RegulationAnalysis
          }
        ]
      }
    ]
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router;
