<template>
  <div>
    <p class="filter-label">{{ labelText }}</p>
    <v-select
      v-model="value"
      :items="isRemote ? items : localItems"
      color="#FF7141"
      item-text="name"
      item-value="id"
      solo
      flat
      hide-details
      @change="changeValue()"
    >
      <template #item="{ item }">
        <span class="select-item-text">{{ item.name }}</span>
      </template>
    </v-select>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'SelectFilter',
  created() {
    this.fetchItems();
    this.$store.state.filtersValues[this.filterName] = this.value;
  },
  methods: {
    fetchItems() {
      axios.get(this.filterUrl).then((response) => {
        this.items = response.data.data;
      });
    },
    changeValue() {
      this.$store.state.filtersValues[this.filterName] = this.value;
    }
  },
  props: ['labelText', 'filterName', 'isRemote', 'filterUrl', 'localItems'],
  data() {
    return {
      value: 1,
      items: []
    };
  }
};
</script>
<style scoped lang="sass">
.theme--light
  .v-input
    width: 260px
    font-size: 16px
    border: 1px solid #7387A0
    transition: border-color $transition-fast
    &:hover
      border-color: $color-dark-blue

.filter-label
  margin-bottom: 7px
  font: 16px/18px $font-bold
  color: $color-dark-blue

.select-item-text
  color: $color-dark-blue
</style>
