<template>
  <v-expansion-panels v-model="panel" focusable flat>
    <v-expansion-panel>
      <v-expansion-panel-header
        @click="changeFilter()"
        hide-actions
        :ripple="false"
      >
        <span class="mr-2 icon icon-funnel"></span>
        <span>Фильтры</span>
        <span class="icon icon-arrow-dropdown"></span>
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-row dense>
          <template v-for="filter in filters">
            <DateFilter
              :key="filter.filterName"
              :ref="`${filter.filterName}`"
              v-if="filter.filterType === 'date'"
              :labelText="filter.labelText"
              :filterName="filter.filterName"
            />
            <SelectFilter
              :key="filter.filterName"
              :ref="`${filter.filterName}`"
              :isRemote="filter.isRemote"
              :localItems="filter.localItems"
              :filterUrl="filter.filterUrl"
              :labelText="filter.labelText"
              :filterName="filter.filterName"
              v-if="filter.filterType === 'select'"
            />
          </template>
        </v-row>
        <FilterButtons
          @updateTable="$emit('reloadTable')"
          @resetFilters="resetAll()"
        />
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import DateFilter from './DateFilter';
import SelectFilter from './SelectFilter';
import FilterButtons from './FilterButtons';

export default {
  name: 'Filters',
  components: {
    DateFilter,
    SelectFilter,
    FilterButtons
  },
  props: ['filters'],
  data() {
    return {
      panel: 0
    };
  },
  methods: {
    resetAll() {
      for (let filter in this.$refs) {
        if (
          this.$store.state.defaultFiltersValues &&
          this.$store.state.defaultFiltersValues[filter]
        ) {
          if (filter === 'date')
            this.$store.state.defaultFiltersValues[
              filter
            ] = new Date().toISOString().substr(0, 10);

          this.$refs[filter][0].value = this.$store.state.defaultFiltersValues[
            filter
          ];
        }
      }
    },
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    }
  },
  created() {
    this.$store.state.filterOpen = true;
  }
};
</script>

<style scoped lang="sass">
.v-expansion-panel-header
  padding: 9px 20px 8px 13px
  line-height: $line-height-root
  color: $color-white
  background-color: $color-blue
  border-radius: 4px
  transition: background-color $transition-normal
  &:before
    content: none
  &:hover
    background-color: #30446E
  & > *:not(.v-expansion-panel-header__icon)
    flex: none
  .icon-funnel
    font-size: 18px
  .icon-arrow-dropdown
    transform: rotate(0deg) translateY(1px)
    margin-left: 14px
    font-size: 12px
    transition: transform $transition-normal
  &--active
    .icon-arrow-dropdown
      transform: rotate(180deg) translateY(0px)
</style>
