<template>
  <v-app>
    <v-navigation-drawer
      id="nav"
      :mini-variant="mini"
      color="#00194D"
      app
      permanent
      :width="300"
      :class="mini ? 'nav-mini' : ''"
      class="nav-bold-text"
      floating
      mini-variant-width="80"
    >
      <div @click="home">
        <img class="ma-5" :src="require(`@/assets/images/${changeLogo}`)" />
      </div>
      <v-list v-if="!mini" dark dense flat>
        <template v-for="item in this.$store.state.menuItems">
          <v-list-group
            v-if="item.children"
            v-model="item.model"
            :class="{
              'last-nested-active parent-nested-active': isChildActive(item)
            }"
            :key="item.text"
            :prepend-icon="item.icon"
            :append-icon="item.arrow"
            :ripple="false"
            @click="closeChildren(item)"
          >
            <template v-slot:activator>
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </template>

            <template v-for="child in item.children">
              <v-list-group
                v-if="child.children"
                sub-group
                v-model="child.model"
                class="pa-0 last-nested"
                :class="{ 'last-nested-active': isChildActive(child) }"
                :key="child.text"
                :prepend-icon="child.icon"
                :ripple="false"
              >
                <template v-slot:activator>
                  <v-list-item-title class="mr-0">
                    {{ child.text }}
                  </v-list-item-title>
                </template>

                <v-list-item
                  v-for="(sub_child, i) in child.children"
                  v-model="sub_child.model"
                  class="plain-text py-0 pl-3 my-0 rounded-l-lg"
                  active-class="is-active"
                  color="#00194d"
                  :key="i"
                  :to="{ path: sub_child.path }"
                  :ripple="false"
                >
                  <v-list-item-title class="text-wrap">
                    {{ sub_child.text }}
                  </v-list-item-title>
                </v-list-item>
              </v-list-group>
              <v-list-item
                v-else
                v-model="child.model"
                class="rounded-l-lg item-no-children"
                active-class="is-active"
                :key="child.text"
                :to="{ path: child.path }"
                :ripple="false"
              >
                <v-list-item-action class="mr-2 my-0">
                  <v-icon>{{ child.icon }}</v-icon>
                </v-list-item-action>
                <v-list-item-content class="py-0">
                  <v-list-item-title>
                    {{ child.text }}
                  </v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </template>
          </v-list-group>
          <v-list-item
            v-else
            v-model="item.model"
            :key="item.text"
            :to="{ path: item.path }"
            :class="mini ? 'rounded-circle' : 'rounded-l-lg'"
            :active-class="mini ? 'active-list-mini' : 'active-list'"
            :prepend-icon="item.icon"
            :ripple="false"
          >
            <v-list-item-action :class="mini ? 'mr-0 my-0' : 'mr-0 my-0'">
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content class="py-0">
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
      <v-list v-else dark dense flat>
        <template v-for="item in this.$store.state.menuItems">
          <v-list-item
            v-if="item.isMiniView"
            v-model="item.model"
            :key="item.text"
            :to="{ path: item.path }"
            :class="mini ? 'rounded-circle' : 'rounded-l-lg'"
            :active-class="mini ? 'active-list-mini' : 'active-list'"
            :prepend-icon="item.icon"
            :ripple="false"
            @click="setChildPath(item)"
          >
            <v-list-item-action :class="mini ? 'mr-0 my-0' : 'mr-2 my-0'">
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content class="py-0">
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <template v-for="child in item.children">
            <v-list-item
              v-if="child.isMiniView"
              v-model="child.model"
              :key="child.text"
              :to="{ path: child.path }"
              :class="mini ? 'rounded-circle' : 'rounded-l-lg'"
              :active-class="mini ? 'active-list-mini' : 'active-list'"
              :prepend-icon="child.icon"
              :ripple="false"
              @click="setChildPath(child)"
            >
              <v-list-item-action :class="mini ? 'mr-0 my-0' : 'mr-2 my-0'">
                <v-icon>{{ child.icon }}</v-icon>
              </v-list-item-action>
              <v-list-item-content class="py-0">
                <v-list-item-title>
                  {{ child.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
        </template>
      </v-list>
      <MenuFooter />
    </v-navigation-drawer>
    <v-main id="main">
      <v-btn class="toggle-btn" @click="toggle" color="#00194d" dark small>
        <v-icon>{{ appMenuIcon }}</v-icon>
      </v-btn>

      <router-view></router-view>
      <Footer />
    </v-main>
  </v-app>
</template>

<script>
import Footer from './components/Footer';
import MenuFooter from './components/MenuFooter';
export default {
  name: 'App',

  components: { MenuFooter, Footer },

  data: () => {
    return {
      mini: false
    };
  },
  created() {
    if (this.$route.path === '/') this.$router.push('/tasks/');
  },
  methods: {
    home() {
      this.$router.push('/tasks/');
    },
    setChildPath(item) {
      if (item.children) {
        this.$router.push(item.children[0].path);
      }
    },
    toggle() {
      this.$store.state.menuMini = this.mini = !this.mini;
      if (!this.mini) {
        this.$store.state.menuItems.forEach((parentElement) => {
          parentElement.model =
            this.$route.path.indexOf(parentElement.path) >= 0;
          if (parentElement.children) {
            parentElement.children.forEach((childElement) => {
              childElement.model =
                this.$route.path.indexOf(childElement.path) >= 0;
              if (childElement.children) {
                childElement.children.forEach((subChild) => {
                  subChild.model = this.$route.path.indexOf(subChild.path) >= 0;
                });
              }
            });
          }
        });
      }
    },
    closeChildren(item) {
      if (!this.mini) {
        this.$store.state.menuItems.forEach((parentElement) => {
          if (parentElement.path === item.path && parentElement.children) {
            parentElement.children.forEach((childElement) => {
              childElement.model = false;
            });
          }
        });
      }
    },
    isChildActive(item) {
      let isActive = false;
      for (let parent of this.$store.state.menuItems) {
        if (parent.path === item.path) {
          isActive = this.$route.path.indexOf(parent.path) >= 0;
        } else {
          if (parent.children) {
            for (let child of parent.children) {
              if (child.path === item.path) {
                isActive = this.$route.path.indexOf(child.path) >= 0;
              }
            }
          }
        }
      }
      return isActive;
    }
  },
  computed: {
    changeLogo() {
      return this.mini ? 'logo-mini.svg' : 'logo.svg';
    },
    appMenuIcon() {
      return this.mini ? 'mdi-chevron-right' : 'mdi-chevron-left';
    }
  }
};
</script>

<style>
/* doesn't work in sass => need to find variable? */
.v-list-group.v-list-group--active.primary--text.parent-nested-active > div,
.v-list-group__header {
  color: #fff !important;
  transition: color 0.3s linear;
}

.last-nested-active > div.v-list-group__header,
.last-nested-active
  > div.theme--dark.v-list-item:not(.v-list-item--active):not(.v-list-item--disabled),
.last-nested-active
  > div.theme--dark.v-list-item:not(.v-list-item--active):not(.v-list-item--disabled)
  .theme--dark.v-icon {
  color: #ff7141 !important;
}
.v-list-group--sub-group .v-list-group__header {
  margin-left: 28px;
}
.last-nested .v-list-group__items {
  padding: 1px 0 2px;
}

.v-list-group__header__prepend-icon .icon {
  transform: none !important;
}
#nav .v-icon {
  font-size: 18px;
}
#nav .icon-arrow-dropdown {
  position: absolute;
  top: 16px;
  right: 20px;
  font-size: 10px;
}
.v-list
  .v-list-group--active
  > .v-list-group__header
  > .v-list-group__header__append-icon
  .v-icon {
  transform: rotate(180deg);
  transition: transform 0.3s linear;
}

.v-list-group__header:hover,
.theme--dark.v-list-item:not(.v-list-item--active):not(.v-list-item--disabled):hover,
.theme--dark.v-list-item:not(.v-list-item--active):not(.v-list-item--disabled):hover
  .theme--dark.v-icon {
  color: #ff7141 !important;
}

.active-list-mini {
  color: #00194d !important;
}
.active-list-mini .v-list-item__action {
  justify-content: center;
}
</style>

<style scoped lang="sass">
@font-face
  font-family: 'FSRAILBook'
  src: local('FSRAILBook'), url('assets/fonts/FSRAILWAYBook.otf') format('opentype')
@font-face
  font-family: 'FSRAILBold'
  src: local('FSRAILBold'), url('assets/fonts/FSRAILWAYBold.otf') format('opentype')
@font-face
  font-family: 'icomoon'
  src: url('assets/fonts/icomoon.ttf') format('truetype')
  font-weight: normal
  font-style: normal
  font-display: block

.plain-text
  margin-left: 78px
  padding-top: 6px !important
  padding-bottom: 6px !important
  .v-list &.v-list-item
    min-height: 0 !important
  div
    font-family: $body-font-family
    font-size: $font-size-root !important
    line-height: 18px

.nav-bold-text
  font-family: $font-bold
  color: $color-dark-blue
  border-radius: 0 5px 5px 0

#main
  .toggle-btn
    background-color: $color-dark-blue
    position: absolute
    left: -5px
    top: 50%
    width: 10px
    min-width: 0

#nav
  padding-bottom: 60px

.v-list
  ::v-deep
    .v-list-item
      min-height: 36px !important
      padding: 0 13px 0 23px
      transition: color $transition-normal
      .nav-mini &
        min-height: 30px !important
        margin-top: 6px
        margin-bottom: 6px
        padding: 0
  .v-list-item__action
    margin-right: 2px !important
    .nav-mini &
      margin-right: 0 !important
      justify-content: center
  &-item
    &.is-active
      cursor: default
      &:not(.item-no-children)
        background-color: $color-light-grey
    &__title.mr-0:after
      content: '\e901'
      position: absolute
      top: 8px
      right: 20px
      font-size: 10px
      font-family: $custom-icon-font
      transition: transform $transition-normal
      .v-list-group--sub-group.v-list-group--active &
        transform: rotate(180deg)
  .item-no-children
    margin-left: 28px
    padding-left: 24px
  .v-list-item--active
    color: $color-orange
    cursor: default
  .v-icon
    transition: color $transition-normal
  .icon-task
    margin: 0 0 4px 2px
    .nav-mini &
      margin: 0
  .icon-list-alt
    margin-bottom: 4px
    .nav-mini &
      margin-bottom: 0

.active-list-mini
  margin-left: 24px
  padding: 0
  width: 30px
  height: 30px
  background-color: $color-light-grey
</style>
