<template>
  <v-tabs v-model="tab" hide-slider>
    <v-tab
      :ripple="false"
      v-for="(item, i) in tableTypeArray"
      :key="item.tab"
      :style="getStyles(i)"
      :to="{ path: item.tablePath }"
    >
      {{ item.tabName }}</v-tab
    >
  </v-tabs>
</template>

<script>
export default {
  name: 'Tabs',
  data() {
    return {
      tab: this.tabModel
    };
  },
  methods: {
    getStyles(i) {
      let pa = i > 0 ? 23 : 16;
      return 'z-index:' + (9 - i) + ';' + 'padding-left:' + pa + 'px;';
    }
  },
  props: ['tableTypeArray', 'tabModel']
};
</script>

