<template>
  <v-text-field
    :value="row[col.value]"
    single-line
    hide-details
    dense
    max="999"
    min="0"
    type="number"
    @input="$emit('updateCell', $event)"
    oninput="if(Number(this.value) > Number(this.max)) this.value = this.value.slice(0, this.max.length); 
          if (this.value === '') this.value = 0;
          if (this.value) this.value = +this.value;"
  ></v-text-field>
</template>

<script>
export default {
  name: 'EditCell',
  props: ['row', 'col', 'oldValue', 'rowId']
};
</script>
