<template>
  <div class="mr-5">
    <p class="filter-label">{{ labelText }}</p>
    <v-menu
      v-model="fromDateMenu"
      :close-on-content-click="false"
      :nudge-right="40"
      class="mt-2"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="290px"
    >
      <template v-slot:activator="{ on }">
        <v-text-field
          solo
          flat
          hide-details
          readonly
          :value="fromDateDisp"
          v-on="on"
          @click:append="showC"
        >
          <template v-slot:append>
            <v-icon color="#FF7141" class="cal-icon" @click="showC"
              >mdi-calendar</v-icon
            >
          </template>
        </v-text-field>
      </template>
      <v-date-picker
        v-model="value"
        no-title
        @input="[closePicker(), setValueInStore(value)]"
        first-day-of-week="1"
      ></v-date-picker>
    </v-menu>
  </div>
</template>

<script>
export default {
  name: 'DateFilter',
  props: ['labelText', 'filterName'],
  data: (vm) => ({
    isShow: false,
    value: new Date().toISOString().substr(0, 10),
    dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
    fromDateMenu: false
  }),
  created() {
    this.$store.state.filtersValues[this.filterName] = this.value;
  },
  computed: {
    fromDateDisp() {
      return this.formatDate(this.value);
    }
  },
  methods: {
    setValueInStore(val) {
      this.$store.state.filtersValues.date = val;
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    showC() {
      this.fromDateMenu = !this.fromDateMenu;
    },
    closePicker() {
      this.fromDateMenu = false;
    }
  }
};
</script>

<style scoped lang="sass">
.theme--light
  .v-input
    width: 120px
    padding: 4px 0
    border: 1px solid #7387A0
    transition: border-color $transition-fast
    &:hover
      border-color: $color-dark-blue

.filter-label
  margin-bottom: 7px
  font: 16px/18px $font-bold
  color: $color-dark-blue

.cal-icon
  font-size: 18px
  margin: -2px
</style>
