<template>
  <div class="pa-5 pb-0">
    <Filters
      :filters="[{ filterName: 'date', filterType: 'date', labelText: 'Дата' }]"
      @reloadTable="updateTable()"
    />
    <Buttons :params="params" :exportUrl="createExportUrl" />
    <Tabs :tableTypeArray="tabs" :tabModel="tabModel" />
    <DataTable
      :headers="headers"
      :url="url"
      :params="{ date: Math.floor(Date.now() / 1000), drl: 1 }"
      ref="table"
      :tableType="tableType"
    />
  </div>
</template>

<script>
import Filters from '../components/Filters';
import Buttons from '../components/Buttons';
import Tabs from '../components/Tabs';
import DataTable from '../components/DataTable';

export default {
  name: 'RegulationJoints',
  components: {
    Buttons,
    Filters,
    Tabs,
    DataTable
  },
  methods: {
    getFormattedDate(date) {
      let dateToParams = date;
      if (typeof dateToParams === 'string') {
        let dateFromFilter = new Date(dateToParams);
        dateToParams = Math.floor(dateFromFilter.getTime() / 1000);
      }
      return dateToParams;
    },
    updateTable() {
      this.$refs.table.fetchTableData({
        date: this.getFormattedDate(this.$store.state.filtersValues.date),
        drl: 1
      });
    }
  },
  computed: {
    createExportUrl() {
      return (
        'method=regulation&tab=region-view&date=' +
        this.getFormattedDate(this.$store.state.filtersValues.date)
      );
    }
  },
  props: ['tabs'],
  data() {
    return {
      panel: 0,
      isFilterOpen: true,
      dateValue: Math.floor(Date.now() / 1000),
      url: '/arm-isujt/server/splices.json',
      tableType: 'reg_join',
      tabModel: 0,
      params: {
        buttons: {
          print: true,
          export: true
        },
        name: 'Оперативное регулирование',
        icon: 'icon icon-settings'
      },
      headers: [
        {
          text: '№ п/п',
          align: 'start',
          value: 'sort_number',
          textMinify: 'mini',
          border: true
        },
        {
          text: 'Междорожные стыковые пункты',
          align: 'start',
          value: 'long_name',
          textMinify: 'mini',
          border: true
        },
        {
          text: 'План-задание передачи поездов',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'на 6:00 ч',
              value: 'plan_train_half',
              alignHeader: 'center',
              border: true,
              editable: true
            },
            {
              text: 'на 18:00 ч',
              value: 'plan_train',
              alignHeader: 'center',
              border: true,
              editable: true
            }
          ]
        },
        {
          text: 'План-задание передачи локомотивов',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'на 6:00 ч',
              value: 'plan_loco_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'на 18:00 ч',
              value: 'plan_loco',
              alignHeader: 'center',
              border: true
            }
          ]
        },
        {
          text: 'План-задание передачи поездо-локомотивов',
          align: 'start',
          alignHeader: 'center',
          children: [
            {
              text: 'на 6:00 ч',
              value: 'plan_train_loco_half',
              alignHeader: 'center',
              border: true
            },
            {
              text: 'на 18:00 ч',
              value: 'plan_train_loco',
              alignHeader: 'center',
              border: true
            }
          ]
        }
      ]
    };
  }
};
</script>
